import React from 'react';

export default (props) =>
{

	let {state} = props.state;
	let {linksList, successGetRight} = state;
	let message;

	if(successGetRight === null)  message = '';
	else if(successGetRight) message = <div className="success message">  .   .</div>;
	else message = <div className="error message">   .   .</div>;

	return(
		<div className="main links_main">
			{message}
			<section className="links_description">
				<div>     .</div>
				<div>{linksList.unique_key}</div>
			</section>
			{props.children}
		</div>
	)

}

	// <div className="error_message">   ,   .</div>